import React, {Component} from 'react'
import './projectContainer.scss'

class ProjectContainer extends Component  {

    render() {
        if (alt === "left") {
            return <Left />
        } else {
            return <Right />
        }
    }

}

const Left = () => (
    <div className="project-container">
        Left
    </div>
)

const Right = () => (
    <div className="project-container">
        right
    </div>
)


export default ProjectContainer